import { Container, Typography, useMediaQuery, Grid } from "@mui/material";
import { makeStyles, useTheme } from "@mui/styles";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import CommonHero from "../../../shared/CommonHero";
import Calendar from "./Calendar";
import Eye from "./Eye";
import arrow from "../../../assets/images/arrow.svg";
import { Swiper, SwiperSlide } from "swiper/react";
import "@fancyapps/ui/dist/fancybox.css";
import { Fancybox } from "@fancyapps/ui";

import "swiper/css";
import "swiper/css/pagination";

import SwiperCore, { Navigation, Pagination } from "swiper";
import Recommendations from "./Recommendations";
import { useDispatch, useSelector } from "react-redux";
import { getArticleData } from "../../../redux/reducers/site";
import { useParams } from "react-router";

SwiperCore.use([Pagination, Navigation]);

const NewsDetails = () => {
  const classes = useStyles();
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.down("lg"));
  const sm = useMediaQuery(theme.breakpoints.down("sm"));

  const dispatch = useDispatch();
  const { id } = useParams();
  const { article } = useSelector(({ site }) => site);
  useEffect(() => {
    dispatch(getArticleData(id));
  }, [id]);

  if (!article.id)
    return (
      <Container maxWidth="xl" sx={{ height: "400px", paddingTop: "50px" }}>
        <Typography ml={1} variant="body2">
          Загрузка статьи
        </Typography>
      </Container>
    );

  return (
    <>
      <CommonHero title={article.title} />
      <section className={classes.section}>
        <Container maxWidth="xl">
          <Grid container>
            <Grid item md={9} xs={12}>
              <Box display="flex" alignItems="center">
                <Box
                  display="flex"
                  alignItems="center"
                  sx={{
                    marginRight: { xs: 3, md: 9 },
                  }}
                >
                  <Calendar />
                  <Typography ml={1} variant="body2">
                    {new Date(article.create_at).toLocaleDateString()}
                  </Typography>
                </Box>
                <Box
                  display="flex"
                  alignItems="center"
                  sx={{
                    marginRight: { xs: 1, sm: 3, md: 9 },
                  }}
                >
                  <Eye />
                  <Typography ml={1} variant="body2">
                    {article.watched_count} Просмотров
                  </Typography>
                </Box>
                <Box
                  sx={{
                    padding: "5px 20px",
                    background: "#FFB951",
                    borderRadius: "30px",
                  }}
                >
                  <Typography variant="body2">{article.category}</Typography>
                </Box>
              </Box>
              <Typography
                variant="body2"
                mt={4}
                mb={2}
                sx={{
                  lineHeight: "150%",
                  fontWeight: "normal",
                }}
                dangerouslySetInnerHTML={{
                  __html: article.description,
                }}
              />
              <img
                src={article.border_photo}
                style={{
                  width: "100%",
                  borderRadius: 6,
                  objectFit: "cover",
                  height: sm ? 116 : 208,
                  marginBottom: "20px",
                }}
                alt=""
              />
              {/*<Typography
                          variant="body2"
                          mt={2}
                          mb={2}
                          sx={{
                            lineHeight: "150%",
                            fontWeight: "normal",
                          }}
                      >
                        {article.description}
                      </Typography>*/}
            </Grid>
            {!lg && <Recommendations article={article} />}
          </Grid>
          {article.gallery.length > 3 && (
            <Box position="relative">
              <Swiper
                slidesPerView={3}
                navigation={true}
                pagination={{
                  clickable: true,
                }}
                className={classes.swiper}
                breakpoints={{
                  0: {
                    slidesPerView: 1,
                    slidesPerGroup: 1,
                    spaceBetween: 0,
                  },

                  768: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                    spaceBetween: 16,
                  },

                  992: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                    spaceBetween: 30,
                  },

                  1200: {
                    slidesPerView: 3,
                    slidesPerGroup: 3,
                    spaceBetween: 30,
                  },
                }}
              >
                {article.gallery.map((item, idx) => (
                  <SwiperSlide style={{ position: "relative" }} key={idx}>
                    <div data-fancybox="gallery" data-src={item}>
                      <img
                        src={item}
                        style={{
                          width: "100%",
                          height: !sm ? 260 : 235,
                          borderRadius: 6,
                          objectFit: "cover",
                        }}
                        alt=""
                      />
                    </div>
                  </SwiperSlide>
                ))}
              </Swiper>
            </Box>
          )}
          {lg && <Recommendations article={article} />}
        </Container>
      </section>
    </>
  );
};

export default NewsDetails;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: "85px 0 120px",
    background: "#F6F7F9",
    position: "relative",

    [theme.breakpoints.down(992)]: {
      padding: "44px 0 120px",
    },

    [theme.breakpoints.down("sm")]: {
      padding: "44px 0 100px",
    },

    "& svg": {
      [theme.breakpoints.down("sm")]: {
        height: 16,
      },
    },
  },

  swiper: {
    position: "static",
    "& .swiper-button-next, .swiper-button-prev": {
      width: 54,
      height: 54,
      background: "#000000",
      position: "absolute",
      top: "50%",
      transform: "translateY(-50%)",
      zIndex: 2,
      borderRadius: "100%",
      opacity: "0.4",
      cursor: "pointer",

      "&:before": {
        content: `''!important`,
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        background: `no-repeat url('${arrow}') center/cover`,
      },

      [theme.breakpoints.down("lg")]: {
        width: 40,
        height: 40,
      },

      [theme.breakpoints.down("sm")]: {
        width: 34,
        height: 34,
      },
    },

    "& .swiper-button-next": {
      right: 0,
      transform: "translateY(-50%) rotate(180deg)",
    },

    "& .swiper-button-disabled": {
      opacity: 0.1,
    },

    "& .swiper-pagination-bullets": {
      bottom: -40,
      "& .swiper-pagination-bullet": {
        width: 16,
        height: 16,
        background: "#FFB951",
        opacity: 1,
        [theme.breakpoints.down("768")]: {
          width: 12,
          height: 12,
        },
      },

      "& .swiper-pagination-bullet-active": {
        background: "#1C1C1C",
        border: "2px solid var(--color-primary)",
      },
    },
  },
}));
