import React, { useEffect } from "react";
import { Typography, Grid } from "@mui/material";
import { Box } from "@mui/system";
import img from "../../../assets/images/News/article1.jpg";
import { Link, NavLink } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getNewsData } from "../../../redux/reducers/site";

const Recommendations = ({ article }) => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getNewsData());
  }, []);

  const { news } = useSelector(({ site }) => site);

  return (
    <Grid item md={3} xs={12} sx={{ pl: { md: "32px" } }}>
      {news.length &&
        news?.filter(
          (articleCategory) =>
            articleCategory.category === article.category &&
            articleCategory.id !== article.id
        ).length >=1 && (
          <Typography
            variant="h6"
            mb={4}
            sx={{
              mt: { xs: 10, lg: 0 },
              mb: { xs: "37px" },
            }}
          >
            Похожие {article.category}
          </Typography>
        )}

      <Grid container>
        {news.length &&
          news
            .filter(
              (articleCategory) =>
                articleCategory.category === article.category &&
                articleCategory.id !== article.id
            )
            .slice(0, 5)
            .map((item, idx) => (
              <Grid
                item
                mb={5}
                sx={{ pr: { xs: 2, lg: 0 } }}
                md={12}
                sm={6}
                xs={12}
                key={idx}
              >
                <NavLink to={`/news/${item.id}`}>
                  <img
                    src={item.border_photo}
                    style={{ height: 160, borderRadius: "6px", minWidth: 270 }}
                    alt=""
                  />
                  <Typography
                    variant="body1"
                    mt={1}
                    mb={1}
                    sx={{ fontWeight: 500 }}
                  >
                    {item.title}
                  </Typography>
                  <Typography
                    variant="body2"
                    sx={{ fontWeight: "300" }}
                    dangerouslySetInnerHTML={{
                      __html: item.description.substring(0, 100) + "...",
                    }}
                  />
                </NavLink>
              </Grid>
            ))}
      </Grid>
    </Grid>
  );
};

export default Recommendations;
