import * as React from "react";
import Link from "@mui/material/Link";
import Typography from "@mui/material/Typography";
import Breadcrumbs from "@mui/material/Breadcrumbs";
import { Link as RouterLink, useLocation } from "react-router-dom";
import { useEffect } from "react";
import { getArticleData } from "../../redux/reducers/site";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";

const LinkRouter = (props) => <Link {...props} component={RouterLink} />;

export const CommonBreadcrumbs = ({ title }) => {
  const dispatch = useDispatch();
  const { id } = useParams();
  const { article } = useSelector(({ site }) => site);


  useEffect(() => {}, [title]);

  const generateNameMap = (id = 1, idKey, map) => {
    const clone = {};
    Object.keys(map).forEach(k => {
      clone[k.replace(idKey, id)] = map[k].replace(idKey, id)
    })
    return clone;
}

  const breadcrumbNameMap = generateNameMap(id, ':id', {
    "/news": "Новости",
    "/news/:id": `${article ? article.title : ""}`,
    "/about": "О компании",
    "/tariffs": "Тарифы",
    "/contacts": "Контакты",
    "/tracking": "Отследить заказ",
  });

  const location = useLocation();
  const pathnames = location.pathname.split("/").filter((x) => x);

  return (
    <Breadcrumbs aria-label="breadcrumb">
      <LinkRouter underline="hover" color="secondary" to="/" variant="body2">
        Главная
      </LinkRouter>
      {pathnames.map((value, index) => {
        const last = index === pathnames.length - 1;
        const to = `/${pathnames.slice(0, index + 1).join("/")}`;

        return last ? (
          <Typography color="secondary" key={to} variant="body2">
            {breadcrumbNameMap[to]}
          </Typography>
        ) : (
          <LinkRouter color="secondary" to={to} key={to} variant="body2">
            {breadcrumbNameMap[to]}
          </LinkRouter>
        );
      })}
    </Breadcrumbs>
  );
};
